.gallery {
  &__photos {
    margin-bottom: 16px;
  }
  
  &__photo-container {
    position: relative;
    padding-bottom: 100%;
    transition: transform 0.3s;
  
    width: 100%;
  
    &:hover {
      transform: scale(1.05);
      
    }
  
    @include onTablet {
      padding-bottom: 0;
  
      height: 187px;
    }
  
    @include onDesktop {
      height: 320px;
    }
  
  }
  
  &__photo {
    position: absolute;
    object-fit: cover;
    object-position: center;
  
    width: 100%;
    height: 100%;
  }
  
  &__title {
    margin-bottom: 8px;
  
    @include thirdTitle;
  }
  
  &__paragraph {
    @include deafultParagraph;
    margin-bottom: 48px;
    max-width: 660px;
  }
}
