.page {
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  
  &__body {
    background: $color-dark;
    color: $color-white;
  
    &--with-menu {
      overflow: hidden;
    }
  }
  
  &__menu {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
  
    transform: translateX(-100%);
    transition: transform $transition-time;

    z-index: 1;
  
    &:target {
      transform: translateX(0);
    }
  }
  
  &__section {
    margin-bottom: 80px;
  
    @include onTablet {
      margin-bottom: 120px;
    }
  
    @include onDesktop {
      margin-bottom: 150px;
    }
  }
  
  &__section-title {
    text-align: center;
  
    font-weight: 500;
    font-size: 32px;
    line-height: 1;
    letter-spacing: -1.5px;
    text-transform: capitalize;
  
    &--last {
      text-transform: none;
    }
  
    @include onTablet {
      font-size: 48px;
    }
  
    @include onDesktop {
      font-size: 64px;
    }
  }
}
