.header {
  background-image: url("../images/header-hq.jpg");
  background-size: cover;
  background-position: 85%;
  
  
  font-weight: 700;
  
  @include onTablet {
    background-position: center;
  }
  
  &__content {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    margin: auto;
    
    width: 100%;
    height: 100vh;
    padding: 30px 0 90px;
  
    @include onTablet {
      padding: 34px 0 130px;
    }
  
    @include onDesktop {
      padding: 36px 0 200px;
      
  
    }
  }
  
  &__top {
    @include topActions;
  }
  
  &__links {
    @include links;
  
    grid-template-columns: var(--icon--size) var(--icon--size);
    gap: 30px;
  
    @include onTablet {
      gap: 40px;
    }
  }
  
  &__title {
    font-size: 36px;
    line-height: 100%;
    display: flex;
    align-self: center;
    letter-spacing: -1.5px;
    white-space: nowrap;
  
    @include onTablet {
      font-size: 64px;
    }
  
    @include onDesktop {
      font-size: 80px;
    }
  }
}
