.compare-bikes {
  &__title {
    margin-bottom: 48px;
    
    @include onDesktop {
      margin-bottom: 56px;
    }
  }
  
  &__item {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  
    @include onTablet {
      &:not(:last-child) {
        margin-bottom: 48px;
      }
    }
  
    @include onDesktop {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }
}
