.contacts {
  &__title {
    margin-bottom: 48px;
  }
  
  &__info {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
  
  &__form {
    margin-bottom: 48px;
  
    @include onTablet {
      margin-bottom: 0;
    }
  }
  
  &__form-field {
    margin-bottom: 16px;
  
    &--textarea {
      margin-bottom: 32px;
    }
  }
}
