.info {
  &__title {
    margin-bottom: 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $color-white;
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: $color-white;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}
