.form-field {
  box-sizing: border-box;
  display: block;
  padding: 14px 16px;
  border: 1px solid transparent;
  
  width: 100%;
  height: 48px;
  
  background: #f8f8fa;
  border-radius: 40px;
  color: $form-color-dark;
  
  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }
  
  &:hover {
    border-color: #d5d7de;
    outline: none;
  
    &::placeholder {
      color: $form-color-dark;
    }
  }
  
  &:focus {
    border-color: $form-color-dark;
    outline: none;
  
    &::placeholder {
      color: $form-color-dark;
    }
  }
  
  &--textarea {
    height: 148px;
    resize: none;
  
    font-family: inherit;
    border-radius: 16px;
  }
}
